<template>
  <ue-title type="caption" :text="item.id" weight="regular" color="grey-lighten-1" padding="x-2" margin="t-2"/>
  <ue-title :text="item.content.headline || 'PR HEADLINE ...'" padding="x-2"/>
  <ue-configurable-card
    :title="item.name"
    style="background-color: transparent;"
    elevation="0"
    hide-separator
    align-center-columnsx
    :items="[
      Object.entries(item.press_release_packages).reduce((acc, [key, value]) => {
        acc[value.packageable.name] = value.name + ' (' + value.packageLanguages.map(v => v.name).join(', ') + ')'
        // acc[value.name] = value.packageLanguages.map(v  => v.name).join(', ')
        return acc;
      }, {}),
      {
        'Content': languageFileNames,
        'Media': mediaFileNames,
        'Date': item.content.date || 'N/A',
      },
      [
        item._price ?? 'N/A',
        item._status ?? 'Draft'
      ],
    ]"
    :actions="[
      {
        name: 'edit',
        icon: 'mdi-pencil',
        color: 'primary'
      }
    ]"
  >
    <template
      #[`segment.1`]="segmentScope"
      >
      <ue-property-list :data="segmentScope.data" no-padding class="ml-n2"></ue-property-list>
      <!-- <ue-filepond-preview show-inline-file-name :source="[{file_name: 'sanal_ofisler_girisimciler_icin_en_iyi_cozum_mu.png', uuid: '674ec2845e1ea7.59641789'}, {file_name: '05.2022.pdf', uuid: '674f10eb9cd431.45881329'}]" />
      <ue-filepond-preview :source="[{file_name: '05.2022.pdf', uuid: '674f10eb9cd431.45881329'}]" /> -->
    </template>
    <template
      #[`segment.3`]="segmentScope"
      >
      <div class="h-100 d-flex flex-wrap align-center justify-space-between">
        <v-chip variant="text" class="text-h5 text-grey-darken-2 px-0">
          <ue-text-display
            :text="item._price"
            :sub-text="'+VAT'"
          />
          <!-- {{ item._price }} <span class="text-caption">+VAT</span> -->
        </v-chip>
        <ue-dynamic-component-renderer :subject="item._status"/>
      </div>
    </template>
    <template
      #[`segment.actions`]="segmentScope"
      >
      <slot name="actions">

      </slot>
    </template>
  </ue-configurable-card>

  <!-- <div class="w-33">
    <ue-filepond-preview :source="languageFiles" show-inline-file-name image-size="24" />
    <v-divider v-if="languageFiles.length" class="my-2"></v-divider>
    <ue-filepond-preview :source="mediaFiles" show-inline-file-name image-size="24" />
  </div> -->

</template>

<script>
  import useIterator, {makeIteratorProps, iterableEmits} from '@/hooks/useIterator';
  import { makeFormatterProps } from '@/hooks/useFormatter';

  const { ignoreFormatters } = makeFormatterProps()

  export default{
  emits: iterableEmits,
  props:{
    ...makeIteratorProps(),
    ignoreFormatters
  },
  setup(props, context){
    return {
      ...useIterator(props, context)
    }
  },
  computed: {

    languageFiles(){
      __log(this.item.fileponds)
      let pattern = /content\.(\d+)_content/
      return this.item.fileponds.filter(v => v.role.match(pattern))
    },
    languageFileNames(){
      return this.languageFiles.map(v => this.getFileName(v)).join(', ')
    },
    mediaFiles(){
      return this.item.fileponds.filter(v => v.role.match(/press_release_image/))
    },
    mediaFileNames(){
      return this.mediaFiles.map(v => this.getFileName(v)).join(', ')
    }
  },
  methods: {
    getFileName(filepond){
      const parts = filepond.file_name.split('.');
      const file_name = parts.slice(0, parts.length - 1).join('.');
      const extension = parts[parts.length - 1];
      return __shorten(file_name, 10) + '.' + extension;
    }
  },
  created(){
    // __log(this.item)
    // __log(JSON.stringify(this.item.fileponds))
    // console.log('ConfigurableCardIterator created', this.item, this.headers, this.iteratorOptions, this.rowActions)
  }

  }
</script>
